<template>
  <div class="edit-identity-status">
    <el-dialog
      :visible.sync="searchVisible"
      :title="$t('IDC.directDebitMandate.creatDirectDebitMandate')"
      width="800px"
      :close-on-click-modal="false"
      @submit.prevent.native
      :before-close="cancel"
    >
      <!-- <el-divider /> -->
      <portal-form
        ref="debitMandateForm"
        :form-data="debitMandateForm"
        :form-config-list="formConfigList"
        :form-rules="formRules"
        :needOperateBtn="false"
        :needBottomLine="false"
        :searchBtnLoading="isSearching"
        :smallAutoColumn="smallAutoColumn"
        :bigAutoColumn="bigAutoColumn"
      >
        <template v-slot:agreedT&C>
          <el-form-item prop="agreedTC" :label="$t('IDC.directDebitMandate.agreedTC')">
            <el-switch
              inactive-value="N"
              active-value="Y"
              :active-text="
                debitMandateForm.agreedTC === 'Y' ? $t('IDC.directDebitMandate.yes') : $t('IDC.directDebitMandate.no')
              "
              :active-color="GlobalStyleVariables.primaryColor"
              :inactive-color="GlobalStyleVariables.fillStrongColor"
              @change="onAgreedTChange($event)"
              v-model="debitMandateForm.agreedTC"
              size="small"
            >
            </el-switch>
          </el-form-item>
        </template>
        <template v-slot:availableDate>
          <el-form-item prop="intervalValueStart" :label="$t('IDC.directDebitMandate.availableDate')">
            <div style="display: flex; gap: 4px; position: relative">
              <el-input v-model="debitMandateForm.intervalValueStart" size="small"></el-input>
              <el-divider />
              <el-input v-model="debitMandateForm.intervalValueEnd" size="small"></el-input>
            </div>
          </el-form-item>
        </template>
      </portal-form>
      <div class="flex flex-jus-end" style="padding-bottom: 20px">
        <el-button id="cancel" class="cancel-button" size="small" @click="cancel">Cancel </el-button>
        <el-button type="primary" size="small" :loading="submitLoading" @click="submitEvn" v-preventReClick>
          Submit
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import PortalForm from '@/components/portal-form/index.vue';
import { changeStatus } from '../../../api/api-customer';
import { debitMandateRules } from '@/views/identifier-center/validator/debitMandate.js';
const frequencyOptions = [
  {
    label: 'HAlf Yearly',
    value: 0,
  },
  {
    label: 'Monthly',
    value: 1,
  },
  {
    label: 'Quarterly',
    value: 2,
  },
  {
    label: 'Yearly',
    value: 3,
  },
];

export default {
  components: {
    PortalForm,
  },
  props: {
    searchVisible: {
      type: Boolean,
      default: false,
    },
    smallAutoColumn: {
      type: Number,
      default: 2,
      validate: (value) => {
        return !(24 % value);
      },
    },
    bigAutoColumn: {
      type: Number,
      default: 2,
      validate: (value) => {
        return !(24 % value);
      },
    },
  },
  data() {
    return {
      isSearching: false,
      submitLoading: false,
      frequencyOptions,
      debitMandateForm: {
        payerIdentityType: '',
        payer: '',
        uniqueReferNumber: '',
        agreedTC: false,
        payerAccountName: '',
        frequency: '',
        firstPaymentDate: '',
        availableDate: '',
        intervalValueStart: '',
        intervalValueEnd: '',
        expiryDate: '',
      },
      formRules: debitMandateRules(this),
    };
  },
  computed: {
    formConfigList() {
      return [
        {
          key: 'payerIdentityType',
          label: this.$t('IDC.directDebitMandate.payerIdentityType'),
          prop: 'payerIdentityType',
          comp: 'el-select',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.select'),
          },
          options: [
            { label: 'All', value: '' },
            { label: 'customer', value: '01' },
            { label: 'group', value: '02' },
            { label: 'organization', value: '03' },
          ],
        },
        {
          key: 'payer',
          label: this.$t('IDC.directDebitMandate.payer'),
          prop: 'payer',
          comp: 'el-input',
          formItemClass: 'form-more-select-box', // 需要这个指定样式，在表单组件中有实现
          slots: [
            {
              name: 'append',
              comp: 'el-link',
              class: 'short-code-icon',
              bind: {
                icon: 'el-icon-more',
                underline: false,
              },
              event: {
                click: () => {
                  this.showShortCodeDialog = true;
                },
                change: (value) => {},
              },
            },
          ],
          bind: {
            clearable: true,
            'popper-append-to-body': false, // 将options的popper放在组件中
            placeholder: this.$t('common.placeholder.enter'),
            'min-width': '500px',
          },
        },
        {
          key: 'uniqueReferNumber',
          label: this.$t('IDC.directDebitMandate.uniqueReferNumber'),
          prop: 'uniqueReferNumber',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          slotName: 'agreedT&C',
        },
        {
          key: 'payerAccountName',
          label: this.$t('IDC.directDebitMandate.payerAccountName'),
          prop: 'payerAccountName',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          key: 'frequency',
          label: this.$t('IDC.directDebitMandate.frequency'),
          prop: 'frequency',
          comp: 'el-select',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.select'),
          },
          options: this.frequencyOptions,
        },
        {
          hidden: this.debitMandateForm.frequency === 0,
          formItemClass: 'form-special-date-box',
          label: this.$t('IDC.directDebitMandate.firstPaymentDate'),
          key: 'firstPaymentDate',
          comp: 'el-date-picker',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            type: 'date',
            format: 'dd/MM/yyyy',
          },
        },
        {
          hidden: this.debitMandateForm.frequency === 2,
          slotName: 'availableDate',
        },
        {
          hidden: this.debitMandateForm.frequency === 3,
          formItemClass: 'form-special-date-box',
          label: this.$t('IDC.directDebitMandate.expiryDate'),
          key: 'expiryDate',
          comp: 'el-date-picker',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            type: 'date',
            format: 'dd/MM/yyyy',
          },
        },
      ];
    },
    statusStyle() {
      return (status) => {
        switch (status) {
          case '03':
            return { 'background-color': '#658DF9' };
          case '06':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '07':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '00':
            return { 'background-color': '#658DF9' };
          case '02':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '04':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '05':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '08':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          default:
            return;
        }
      };
    },
  },
  created() {},
  methods: {
    showStatus(val) {
      switch (val) {
        case 'Active':
          return '03';
        case 'Closed':
          return '06';
        case 'Capped':
          return '07';
        case 'New':
          return '00';
        case 'Pending Active':
          return '02';
        case 'Suspended':
          return '04';
        case 'Frozen':
          return '05';
        case 'Dormant':
          return '08';
        default:
          return;
      }
    },
    cancel() {
      this.$refs.debitMandateForm.resetFields();
      this.debitMandateForm.intervalValueEnd = '';
      this.$emit('update:searchVisible', false);
    },
    submitEvn() {
      this.$refs['debitMandateForm'].validate((valid) => {
        if (valid) {
          let params = {
            oldStatus: this.showStatus(this.identityStatus),
            newStatus: this.submitData.status,
            remark: this.submitData.remark,
            receiverIdentityId: this.identityId,
          };
          changeStatus(params).then((res) => {
            if (res.header && res.header.success) {
              this.$modal.msgSuccess(`Status edited successfully.`);
              this.cancel();
              this.$emit('closeSubmit');
            }
          });
        }
      });
    },
    changeType(e) {
      this.$refs['debitMandateForm'].innerFormRef.resetFields();
      this.$refs['debitMandateForm'].innerFormRef.clearValidate();
      this.debitMandateForm.searchType = e;
    },
    onAgreedTChange() {},
  },
  mounted() {},
};
</script>
