.edit-identity-status {
  /deep/textarea::-webkit-input-placeholder {
    -webkit-text-fill-color: $--text_color--gray !important;
    color: $--text_color--gray;
    font-size: 14px;
    font-family: Huawei Sans;
  }
  /deep/ .el-form .el-form-item .el-form-item__label {
    color: #5c5c5c !important;
  }
  /deep/ .el-dialog__headerbtn .el-dialog__close {
    color: $--text_color--gray !important;
    font-size: 16px;
  }
  /deep/ .el-dialog .el-dialog__header .el-dialog__headerbtn {
    color: $--text_color--gray !important;
    font-size: 16px;
    top: 16px;
    right: 16px;
  }
  /deep/ .el-input__inner:hover {
    border-color: $--text_color--secondary !important;
  }
  /deep/ .form-flex-container {
    padding: 0 !important;
  }
  /deep/ .el-divider--horizontal {
    width: 20px;
    top: 5px;
  }
}
</style>
