import { inputRequiredValidateObj, selectRequiredValidateObj } from '@/common/validator/common';

let checkAvailableDateRange = (scope) => {
  return {
    trigger: 'blur',
    required: false,
    validator: (rule, value, callback) => {
      let msg = '';
      let valueStart = scope.debitMandateForm.intervalValueStart;
      let valueEnd = scope.debitMandateForm.intervalValueEnd;
      if (!valueStart || !valueEnd) {
        msg = 'Please enter content.';
        return callback(new Error(msg));
      }
      if (valueStart > valueEnd) {
        msg = 'valueEnd is grater of valueStart.';
        return callback(new Error(msg));
      }
      const minVal = 1;
      const maxVal = 31;

      if (
        valueStart >= minVal &&
        valueStart <= maxVal &&
        valueEnd >= minVal &&
        valueEnd <= maxVal &&
        valueStart <= valueEnd
      ) {
        return callback();
      }
      msg = `Must be between ${minVal} and ${maxVal} characters`;
      callback(new Error(msg));
    },
  };
};
const debitMandateRules = (scope) => {
  return {
    payerIdentityType: [selectRequiredValidateObj],
    payer: [inputRequiredValidateObj],
    uniqueReferNumber: [inputRequiredValidateObj],
    frequency: [selectRequiredValidateObj],
    intervalValueStart: [checkAvailableDateRange(scope)],
  };
};

export { debitMandateRules };
