<template>
  <div class="business-portal-page">
    <portal-header :projectObj="titleConfig" />
    <portal-form
      ref="searchList"
      :form-data="formData"
      :form-rules="formRules"
      :form-config-list="formConfigList"
      :searchBtnLoading="searchLoading"
      @common-enter-event="search"
      @reset-btn-click="reset"
      @search-btn-click="search"
    />
    <div style="padding-left: 20px; margin-top: 20px">
      <div class="row-btn">
        <el-button type="primary" size="small" @click="gotoCreatePage" icon="el-icon-plus">
          {{ $t('common.button.create') }}
        </el-button>
      </div>
    </div>
    <div style="padding: 20px">
      <table-page
        size="small"
        :has-pagination="true"
        :isSearch="isSearch"
        :initializingLoading="true"
        :columns="debitMandateColumns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :isCheck="true"
        @selection-change="handleSelectionChange"
        :sortArr="['status']"
        :customSorting="true"
        idKey="planID"
      >
        <template slot="status" slot-scope="scope">
          <div class="flex flex-align">
            <span class="status-circle" :style="showStatus()"></span>
            <span>{{ scope.data.row || '-' }}</span>
          </div>
        </template>
        <template slot="operation" slot-scope="scope">
          <el-button
            v-if="formatStatus(scope.data.row.status)"
            :id="'bulk-template-detail-' + scope.data.$index"
            type="text"
            size="small"
            @click="handleDetailClick(scope.data.row, 'details')"
          >
            {{ $t('common.button.detail') }}
          </el-button>
          <el-button
            v-else
            :id="'bulk-template-detail-' + scope.data.$index"
            type="text"
            size="small"
            @click="handleDetailClick(scope.data.row, 'process')"
          >
            {{ $t('BUC.taskPlan.process') }}
          </el-button>
          <el-button type="text" size="small" @click="cancel">Cancel</el-button>
          <el-button type="text" size="small" @click="reactivate">Reactivate</el-button>
        </template>
      </table-page>
    </div>

    <portal-short-code
      :search-visible.sync="showShortCodeDialog"
      :short-code.sync="formData.shortCode"
    ></portal-short-code>
    <create-Dialog
      :check="true"
      :data-loader="loadDebitMandates"
      :single-check="false"
      :search-visible.sync="showCreatDialog"
    >
    </create-Dialog>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import PortalForm from '@/components/portal-form';
import TablePage from '@/components/tablePage';
import PortalShortCode from '@/components/portal-short-code/index.vue';
import createDialog from '@/views/identifier-center/pages/direct-debit-mandate/components/create-dialog.vue';
import { queryOrgListByPage } from '@/views/identifier-center/api/api-organization.js';
import { mapGetters } from 'vuex';
import dayjs from 'dayjs';

export default {
  name: 'TaskPlanList',
  components: {
    PortalHeader,
    PortalForm,
    TablePage,
    PortalShortCode,
    createDialog,
  },
  data() {
    return {
      titleConfig: {
        title: this.$t('IDC.directDebitMandate.pageTitle'),
        desc: this.$t('IDC.directDebitMandate.pageDesc'),
      },
      formData: {
        mandateId: null,
        status: null,
        creationDate: [
          dayjs(new Date(dayjs().year(), dayjs().month(), dayjs().date(), 0, 0, 0)).format('YYYYMMDDHHmmss'),
          dayjs(new Date(dayjs().year(), dayjs().month(), dayjs().date(), 23, 59, 59)).format('YYYYMMDDHHmmss'),
        ],
        payerType: '',
        payer: '',
        uniqueReferNumber: '',
      },
      paginationConfig: {
        pageIndex: 1,
        pageSize: 10,
      },
      isSearch: false,
      searchLoading: false,
      debitMandateColumns: [
        {
          label: this.$t('IDC.directDebitMandate.mandateId'),
          prop: 'mandateId',
          width: '200px',
          align: 'left',
        },
        {
          label: this.$t('IDC.directDebitMandate.payerType'),
          prop: 'payerType',
          minWidth: '200px',
          align: 'left',
        },
        {
          label: this.$t('IDC.directDebitMandate.payer'),
          prop: 'payer',
          minWidth: '200px',
          align: 'left',
        },
        {
          label: this.$t('IDC.directDebitMandate.status'),
          prop: 'status',
          __slotName: 'status',
          minWidth: '200px',
          align: 'left',
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.directDebitMandate.uniqueReferNumber'),
          prop: 'uniqueReferNumber',
          minWidth: '220px',
          align: 'left',
        },
        {
          label: this.$t('IDC.directDebitMandate.firstPaymentDate'),
          prop: 'firstPaymentDate',
          minWidth: '200px',
          align: 'left',
        },
        {
          label: this.$t('IDC.directDebitMandate.frequency'),
          prop: 'frequency',
          minWidth: '200px',
          align: 'left',
        },
        {
          label: this.$t('IDC.directDebitMandate.availableDate'),
          prop: 'availableDate',
          minWidth: '280px',
          align: 'left',
        },
        {
          label: this.$t('IDC.directDebitMandate.expiryDate'),
          prop: 'expiryDate',
          minWidth: '200px',
          align: 'left',
        },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          width: '220',
          fixed: 'right',
        },
      ],
      showCreatDialog: false,
      showShortCodeDialog: false,
    };
  },
  computed: {
    formRules() {
      return {};
    },
    formConfigList() {
      return [
        {
          key: 'mandateId',
          label: this.$t('IDC.directDebitMandate.mandateId'),
          prop: 'mandateId',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          key: 'payerType',
          label: this.$t('IDC.directDebitMandate.payerType'),
          prop: 'payerType',
          comp: 'el-select',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.select'),
          },
          options: [
            { label: 'All', value: '' },
            { label: 'customer', value: '01' },
            { label: 'group', value: '02' },
            { label: 'organization', value: '03' },
          ],
        },
        {
          key: 'payer',
          label: this.$t('IDC.directDebitMandate.payer'),
          prop: 'payer',
          comp: 'el-input',
          formItemClass: 'form-more-select-box', // 需要这个指定样式，在表单组件中有实现
          slots: [
            {
              name: 'append',
              comp: 'el-link',
              class: 'short-code-icon',
              bind: {
                icon: 'el-icon-more',
                underline: false,
              },
              event: {
                click: () => {
                  this.showShortCodeDialog = true;
                },
                change: () => {},
              },
            },
          ],
          bind: {
            clearable: true,
            'popper-append-to-body': false, // 将options的popper放在组件中
            placeholder: this.$t('common.placeholder.enter'),
            'min-width': '500px',
          },
        },
        {
          key: 'status',
          label: this.$t('IDC.directDebitMandate.status'),
          prop: 'status',
          comp: 'el-select',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.select'),
          },
          options: [
            { label: 'All', value: '' },
            { label: 'Active', value: '01' },
            { label: 'Cancelled', value: '02' },
            { label: 'Pending Active', value: '03' },
            { label: 'Pending Approve', value: '04' },
          ],
        },
        {
          key: 'uniqueReferNumber',
          label: this.$t('IDC.directDebitMandate.uniqueReferNumber'),
          prop: 'uniqueReferNumber',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          formItemClass: 'form-special-date-box',
          label: this.$t('IDC.directDebitMandate.creationDate'),
          key: 'creationDate',
          comp: 'el-date-picker',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            type: 'daterange',
            format: 'dd/MM/yyyy',
            'range-separator': '-',
            'start-placeholder': 'Start',
            'end-placeholder': 'End',
          },
        },
      ];
    },
    queryParams() {
      const { planName, bulkTypeID, status, scheduleTime, createOperId, approveOperId } = this.formData;
      const { pageIndex, pageSize } = this.paginationConfig;
      let startTime = '';
      let endTime = '';
      if (scheduleTime) {
        startTime = scheduleTime[0];
        endTime = scheduleTime[1];
      }
      return {
        planName,
        bulkTypeID,
        status,
        createOperId,
        approveOperId,
        startTime: startTime,
        endTime: endTime,
        offset: (pageIndex - 1) * pageSize,
        limit: pageSize,
      };
    },
    ...mapGetters('datadict', ['moduleDict']),
  },
  methods: {
    search() {
      this.$refs['searchList'].validate((valid) => {
        if (valid) {
          // 点击search起始页应该为1
          this.paginationConfig.pageIndex = 1;
          this.searchLoading = true;
          this.isSearch = true;
        }
      });
    },

    async getTableData() {
      let params = {};
      try {
        let res = await queryOrgListByPage(params);
        if (!res.totalRecords) {
          res.totalRecords = res.totalRecord || 0;
        }
        return res;
      } catch (e) {
        return {
          header: {
            success: false,
          },
        };
      } finally {
        this.isSearching = false;
      }
    },
    reset() {
      this.paginationConfig.pageIndex = 1;
      this.$refs.searchList.innerFormRef.resetFields();
      this.isSearch = true;
    },
    handleSelectionChange() {},

    handleDetailClick(row, type) {
      let params = {
        type: type,
      };
      this.$router.push({
        path: '../../bulk/bulkTask/details',
        query: { params: encodeURIComponent(JSON.stringify(params)) },
      });
    },
    formatStatus() {},
    showStatus(val) {
      switch (val) {
        case 'Initial':
          return `background-color:${this.GlobalStyleVariables.backgroundColor};border:1px solid #E0E0E0;`;
        case 'Processing':
          return { 'background-color': this.GlobalStyleVariables.statusNoticeColor };
        case 'Checking':
          return `background-color:${this.GlobalStyleVariables.backgroundColor};border:1px solid ${this.GlobalStyleVariables.initialColor}`;
        case 'Suspend':
          return { 'background-color': this.GlobalStyleVariables.statusColorwarning };
        case 'Wait Process':
          return { 'background-color': this.GlobalStyleVariables.statusColorwarning };
        case 'Wait Active':
          return { 'background-color': this.GlobalStyleVariables.statusColorwarning };
        case 'Dis Active':
          return { 'background-color': this.GlobalStyleVariables.statusDark };
        case 'Failed':
          return { 'background-color': this.GlobalStyleVariables.statusErrorColor };
        case 'Expire':
          return { 'background-color': this.GlobalStyleVariables.statusDark };
        case 'Completed':
          return { 'background-color': this.GlobalStyleVariables.statusSuccessColor };
        case 'Canceled':
          return { 'background-color': this.GlobalStyleVariables.statusDark };
        case 'Deactive':
          return { 'background-color': this.GlobalStyleVariables.statusDark };
        default:
          break;
      }
    },
    async loadDebitMandates(param) {
      let res = await queryOrgListByPage(param);
      if (res.header.success) {
        res.totalRecords = res.total;
      }
      return res;
    },
    gotoCreatePage() {
      this.showCreatDialog = true;
    },
    cancel() {
      this.$confirm(this.$t('IDC.directDebitMandate.cancelContent'), this.$t('IDC.directDebitMandate.cancelTitle'), {
        confirmButtonText: 'Submit',
        cancelButtonText: 'Cancel',
        closeOnClickModal: false,
        type: 'warning',
      }).then(() => {});
    },
    reactivate() {
      this.$confirm(
        this.$t('IDC.directDebitMandate.activateContent'),
        this.$t('IDC.directDebitMandate.activateTitle'),
        {
          confirmButtonText: 'Submit',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
          type: 'warning',
        }
      ).then(() => {});
    },
  },
  created() {},
};
</script>

<style lang="scss" scoped>
.direct-debit-mandate {
}
</style>
